* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

header {
  z-index: 100;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa-arrow-left {
  padding-right: 1rem;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.signout-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover {
  color: #6b2da8;
}

.signout-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding: 5rem 12%;
  display: flex;
}

.row1 {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 40%;
  display: flex;
}

.row2 {
  width: 100%;
  height: 60%;
}

.select-container {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.dropdown {
  color: #8d8d8d;
  border: 1px solid #d3d3d3;
  border-radius: .5rem;
  outline: none;
  padding: .3rem .5rem;
  transition: all .3s;
}

.type-options, .months-option, .day-option {
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  transform: translateY(20px);
}

.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

option {
  color: #8d8d8d;
}

.bargraph {
  background-color: #efefef;
  border-radius: 1rem;
  flex-direction: column;
  width: 50%;
  height: 100%;
  padding: 1rem 1rem 2rem;
  display: flex;
}

.users-total {
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: 1rem;
  width: 20%;
  height: 100%;
}

.doughnut-graph-container {
  background-color: #efefef;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 250px;
  padding: 1rem;
  display: flex;
  position: relative;
}

.label-doughnut {
  color: #6b2da8;
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.line-graph-container {
  background-color: #efefef;
  border-radius: 1rem;
  width: 70%;
  height: 100%;
  padding: 1rem;
}

.blogs {
  width: 30%;
  height: 100%;
}

#idvalidated-graph {
  z-index: 10;
  position: relative;
}

@media (width <= 480px) {
  .fa-arrow-left {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .row1 {
    flex-direction: column;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .bargraph {
    height: 300px;
  }

  .doughnut-graph-container {
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 150px;
    display: flex;
    position: relative;
  }

  section {
    width: 100%;
    height: auto;
    padding: 5rem 8%;
  }
}
